#nav-bar {
  color: white;
  background: black;
  width: 100vw;
  height: 10vh;
  left: 0px;
  top: 0px;
  position: fixed;
  display: flex;
  flex-direction: column;
  z-index: 100;
}

#links-section {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  height: 40%;
}

.nav-links {
  text-decoration: none;
  color: white;
  font-size: 15px;
  font-family: Ubuntu-Light, sans-serif;
}

.nav-links:link {
  text-decoration: none;
  color: white;
}

.nav-links:hover {
  text-decoration: none;
  color: white;
}

.nav-links:visited {
  text-decoration: none;
  color: white;
}

.nav-links:active {
  text-decoration: none;
  color: white;
}

@font-face {
  font-family: "Montserrat-Medium";
  src: local("Montserrat-Medium"),
    url("../fonts/Montserrat-Medium.ttf") format("truetype");
}

@font-face {
  font-family: "Ubuntu-Light";
  src: local("Ubuntu-Light"),
    url("../fonts/Ubuntu-Light.ttf") format("truetype");
}
