.project-section {
  margin-top: 17vh;
  font-family: Ubuntu-Light;
  font-size: 16px;
}

#projects-intro {
  margin-top: 20vh;
}

#projects-title {
  font-size: 2em;
  font-family: Ubuntu-Light;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 5vh;
}

#water-tracker-img {
  border-radius: 15px;
}

#water-tracker-link, #stats-link, #file-cabinet-link, #drum-link, #markdown-link, #quote-link, #survey-link, #snake-game-link {
  text-decoration: none;
}

.project {
  margin-bottom: 50px;
}

.project-img {
  max-width: 600px;
  width: 60vw;
  margin-left: auto;
  margin-right: auto;
}

.project-img-label {
  height: 50px;
  width: 250px;
  background-color: black;
  margin-top: 10px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  color: white;
  font-weight: 800;
  font-size: 1em;
  border-radius: 20px;
  line-height: 50px;
  font-family: Ubuntu-Medium;
}

.project-description {
  margin-top: 5vh;
  width: 320px;
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

@font-face {
  font-family: "Ubuntu-Light";
  src: local("Ubuntu-Light"),
    url("../fonts/Ubuntu-Light.ttf") format("truetype");
}

@font-face {
  font-family: "Ubuntu-Medium";
  src: local("Ubuntu-Medium"),
    url("../fonts/Ubuntu-Medium.ttf") format("truetype");
}
