#tech-section {
  margin-bottom: 50px;
  max-width: 900px;
  margin-top: 5vh;
  display: block;
  margin-left: auto;
  margin-right: auto;
  font-family: Ubuntu-Light;
}

.row {
  margin-bottom: 2vh;
}

.img-div {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

#tech-intro {
  margin-bottom: 2vh;
  font-size: 28px;
}

#java {
  margin-left: 3vw;
}

#tech-imgs {
  margin-top: 9vh;
}

.img-responsive {
  width: 8vw;
  max-width: 100px;
}

#click-me {
  opacity: 0;
  display: inline-block;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  width: 85px;
  margin-left: -42.5px;
}

#c-label {
  margin-left: auto;
  margin-right: auto;
}

.img-label {
  padding-top: 5vh;
  font-size: 14px;
}

@media (max-width: 500px) {
  #click-me {
    margin-left: -5vw;
  }
}

@font-face {
  font-family: "Ubuntu-Light";
  src: local("Ubuntu-Light"),
    url("../fonts/Ubuntu-Light.ttf") format("truetype");
}
