#welcome-section {
  margin-top: 50vh;
  width: 80%;
  font-size: 14px;
  margin-left: 15vw;
  margin-bottom: 30vh;
  color: white;
}

#welcome-title {
  margin-top: 15vh;
  font-family: Boogaloo-Regular, sans-serif;
  font-size: 2em;
  transition-property: font-size, color;
  transition-duration: 3s;
  transition-delay: 0s;
}

#welcome-intro {
  font-family: Boogaloo-Regular, sans-serif;
  font-size: 1.5em;
}

#welcome-summary {
  font-family: Ubuntu-Light;
  width: 60vw;
  max-width: 300px;
}

#projects-link {
  font-family: Ubuntu-Light;
}

#button-link {
  border: 2px solid white;
  background: none;
  color: white;
}

@media (min-width: 700px) {
  #welcome-section {
    margin-left: 22vw;
  }
}

@media (max-width: 500px) {
  #welcome-section {
    margin-top: 45vh;
    margin-bottom: 30vh;
  }
}

@font-face {
  font-family: "Boogaloo-Regular";
  src: local("Boogaloo-Regular"),
    url("../fonts/Boogaloo-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Ubuntu-Light";
  src: local("Ubuntu-Light"),
    url("../fonts/Ubuntu-Light.ttf") format("truetype");
}
