#project-nav-bar {
  color: white;
  background: rgb(60,60,60);
  width: 100vw;
  height: 5vh;
  left: 0px;
  top: 10vh;
  position: fixed;
  display: flex;
  flex-direction: column;
  z-index: 100;
  overflow-x: auto;
  flex-wrap: nowrap;
}

#project-links-section {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  overflow-x: auto;
  white-space: nowrap;
}

.vl {
  border-left: 1px solid black;
  height: 5vh;
  top: 0px;
}

.nav-links:link {
  text-decoration: none;
  color: white;
}

.nav-links:hover {
  text-decoration: none;
  color: white;
}

.nav-links:visited {
  text-decoration: none;
  color: white;
}

.nav-links:active {
  text-decoration: none;
  color: white;

}

@font-face {
  font-family: "Montserrat-Medium";
  src: local("Montserrat-Medium"),
    url("../fonts/Montserrat-Medium.ttf") format("truetype");
}

@font-face {
  font-family: "Ubuntu-Light";
  src: local("Ubuntu-Light"),
    url("../fonts/Ubuntu-Light.ttf") format("truetype");
}
