#about-me {
  margin-top: 10vh;
  font-family: Ubuntu-Light;
  margin-left: auto;
  margin-right: auto;
  width: 60%;
  display: block;
}

#about-me-title {
  font-size: 28px;
  text-align: left;
}

#intro-summary{
  display: flex;
  justify-content: space-around;
  margin-top: 5vh;
  margin-right: 5vw;
  font-size: 16px;
}

#profile-pic {
  background-image: url("../images/profile-pic.jpg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  height: 500px;
  width: 60%;
  margin-top: 10vh;
  margin-left: auto;
  margin-right: auto;
  left: 0px;
  top: 0px;
  /* position: fixed;
  z-index: 1;
  max-width: 200px;
  max-height: 200px;
  min-width: 150px;
  min-height: 150px;
  width: 150px;
  height: 150px;
  object-fit: cover;
  object-position: right;
  display: block;
  float: right;
  margin-left: 4vw;
  margin-top: 30px; */
}

@media (max-width: 850px) {
  #about-me {
    width: 90%;
    margin-left: 5vw;
  }

  #profile-pic {
    width: 90%;
  }

  #about-me-summary {
    text-align: justify;
    float: left;
  }

}



@font-face {
  font-family: "Ubuntu-Light";
  src: local("Ubuntu-Light"),
    url("../fonts/Ubuntu-Light.ttf") format("truetype");
}
